.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 40px;
}

.header h1{
  color: hsl(183, 100%, 15%);
  text-transform: uppercase;
}

.card{
  background-color: white;
  width: 800px;
  height: 400px;
  padding: 30px;
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.left{
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.left div{
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.bill{
  background-color: hsl(189, 41%, 97%);
  width: 400px;
  border-radius: 5px;
  position: relative;
}

.bill input{
  /* all: unset; */
  color: hsl(183, 100%, 15%) !important;
  background-color: transparent;
  width: 100%;
  font-size: 22px;
  font-weight: 700;
  /* padding: 5px; */
  height: 50px;
  padding-right: 20px;
  outline: none;
  border: none;
  
}

.bill img{
  position: absolute;
  top: 28%;
  width: 15px;
  left: 10px;
}

.tip{
  display: flex;
  flex-direction: row !important;
  flex-wrap: wrap;
  width: 400px;
}

.tip .per{
  width: 31%;
  height: 50px;
  background-color:  hsl(183, 100%, 15%);
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 22px;
  cursor: pointer;
  font-weight: 700;
  outline: none;
  border: none;
}

.tip .per:focus{
  background-color: hsl(185, 41%, 84%);
  color: hsl(183, 100%, 15%);
}

.custom{
  width: 31%;
  height: 50px;
  border-radius: 5px;
  outline: hsl(172, 67%, 45%) 2px solid ;
  border: none;
  font-size: 22px;
  font-weight: 700;
  padding: 5px;
  color: hsl(183, 100%, 15%);
  animation: forwards .3s flip linear 1;
}

.custom{
  background-color: hsl(189, 41%, 97%) !important;
  color: hsl(184, 14%, 56%) !important;
}

.right{
  width: 320px;
  height: 100%;
  background-color: hsl(183, 100%, 15%);
  padding: 20px;
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.right .container div{
  display: flex;
  justify-content: space-between;
  
}
.right .container{
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.right div p{
  display: flex;
  flex-direction: column;
  color: white;
  font-weight: 700;
  font-size: 20px;
}

.right div p span{
  font-size: 14px;
  font-weight: 400;
}

.right div h1{
  color: hsl(172, 67%, 45%);
  font-size: 2.5em;
}

.reset{
  background-color: hsl(172, 67%, 45%);
  color: hsl(183, 100%, 15%);
  text-transform: uppercase;
  outline: none;
  border: none;
  height: 40px;
  font-size: 20px;
  font-weight: 700;
  border-radius: 3px;
  cursor: pointer;
}

@keyframes flip{
  from{
    transform: scale(0);
  }
  to{
    transform: scale(1);
  }
}